import { SubmitHandler, useForm } from "react-hook-form";
import { IMyForm } from "../interfaces/IMyForm";
import { useTasks } from "../hooks/useTasks";
import styled from "styled-components";

const SiteForm = styled.form`
  color: --text-color;
  display: flex;
  width: 500px;
  margin: 5rem auto;
  flex-direction: column;
  gap: 10px;
`;

const FormInput = styled.input`
  color: --text-color;
  padding: 0.7rem 1.7rem;
  margin: 5px;
  border-radius: 10px;
`

const FormButton = styled.button`
  color: --text-color;
  padding: 0.5rem 1rem;
  border: 0;
  border-radius: 5px;
  font-size: 17px;
  &:hover {
    opacity: .7;
  }
  &:disabled{
    opacity: .2;
  }
`

const FormLabel  = styled.label`
  display: flex;
  flex-direction: column;
  gap: 10px;
`

export const Form: React.FC = () => {
const {tasks, setTasks} = useTasks();
  const saveElement: SubmitHandler<IMyForm> = data => {
    setTasks((prev) => [...prev, data]);
    reset();
  }
  const {
    register,
    handleSubmit,
    formState: { errors, isValid },
    reset,
  } = useForm<IMyForm>({
    mode: "onBlur",
  });
  return (
    <SiteForm onSubmit={handleSubmit(saveElement)}>
      <FormLabel>
        :
        <FormInput
        placeholder="  "
        type="text"
          {...register("name", {
            required: "  ",
            minLength: {
              value: 1,
              message: " ",
            },
          })}
        />
      </FormLabel>
      <FormLabel>
        :
        <FormInput
        placeholder="  "
        type="number"
          {...register("age", {
            required: "  ",
            minLength: {
              value: 2,
              message: " ",
            },
          })}
        />
      </FormLabel>
      <FormLabel>
        :
        <FormInput
        placeholder="  "
        type="email"
          {...register("email", {
            required: "  ",
            pattern: /^[\w-.]+@([\w-]+\.)+[\w-]{2,4}$/,
            minLength: {
              value: 10,
              message: "   example@site.ru",
            },
          })}
        />
      </FormLabel>
      <FormLabel>
         :
        <FormInput
        placeholder="   "
          {...register("phoneNumber", {
            required: "  ",
            pattern: /^\d{10}$/,
            maxLength: {
              value: 15,
              message: "    +",
            },
          })}
        />
      </FormLabel>
      <p>{errors.name?.message}</p>
      <FormButton disabled={!isValid} type="submit"></FormButton>
      {tasks.map((task) => (
        <p>
            {task.name} - {task.age}
        </p>
      ))}
    </SiteForm>
  );
};
